
<template>
    <section>
      
      <loader :is-visible="isLoading"></loader>   
 
    <tg-list
        :listType="['List']"
        :IsShowAction="true"
        ModuleName="ExpenseApproval"
        SubModuleCode="ExpenseApproval"
        IdentityColumn="EXPENSEID"
        :showCheckBox="true"
        :HeaderText="$t('ExpenseApproval')"
        :ListData="ExpenseApprovalData"
        :HeaderData="Headers"
        :HeaderButtons="listheaderbuttons"
        :TotalRecords="TotalRecords"      
        :SearchFields="leftSearchFields" 
        :RenderRowActionMethod="renderActions"
        :ListDataCallBackFunction="FetchData"       
        :SortExp="SortExp"
        :SortBy="SortBy"      
        :LegendArray="LegendArray"
        :NorecordfoundText="$t('NoRecordfound')"
        v-on:LeftsearchButtonClick="leftCommonSearch" 
        v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick" 
        v-on:ActionButtonClick="actionButtonClick"
        v-on:SortdataButtonClick="sortdata" 
        >   
        <template v-slot:slotdata="{data}">
            <template v-if="data.column.COLUMN_NAME == 'Status'">                
                <a class="inactive-disabled" >
                    <span v-bind:class="getStatusClass(data.row.Status, 'Status')" v-if="data.row.Status !='SentForCorrection'">
                        {{data.row[data.column.COLUMN_NAME]}}
                    </span>
                    <span v-bind:class="getStatusClass(data.row.Status, 'Status')" v-if="data.row.Status =='SentForCorrection'">
                        {{ $t('SentForCorrection') }}
                    </span>
                </a>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'Amount'">                                  
                    {{convertDecimalValueInFormat(data.row.Amount) +' '+data.row.ISO_CODE}}                   
            </template>           
        </template>          
    </tg-list>
    <ExpenseComments v-if="showCommentModal" @CloseExpenseComments="CloseExpenseComments()" :access="access" 
        v-bind:expenseid="expid" :CommentHeader="CommentHeader" :isSingleCommentforMultipleExpenses="isSingleCommentforMultipleExpenses"
        :statuscode="statuscode" @fetchdata="FetchData" :visibility="visibility"></ExpenseComments> 
    <ExpenseApproverStatus v-if="showApproverStatusModal" @CloseExpenseApproverStatus="CloseExpenseApproverStatus()" 
        v-bind:expenseid="expid"></ExpenseApproverStatus>
     <ExpenseAttachents v-if="showFileModal" @CloseExpenseAttachments='CloseExpenseAttachments' v-bind:expenseid="expid" 
        v-bind:code="statuscode" ></ExpenseAttachents>
    </section>
</template>
<script>
   import ExpenseApprovalDataServices from "@/services/DataService";
    import Schema from "../ExpenseApproval/ExpenseApprovalSchema";
    import ExpenseComments from '../../../components/Common/Modal/ExpenseComments.vue';
    import ExpenseAttachents from '../../../components/Common/Modal/ExpenseAttachments.vue';
    import ExpenseApproverStatus from '../../../components/Common/Modal/ApproverStatus.vue'
export default {
    components: {        
        ExpenseComments,
        ExpenseApproverStatus,
        ExpenseAttachents,
      
      },
    data(){
        return{            
            isLoading:false,
            widgets:[],
            showFileModal:false,
            searchCondition: '',
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',            
            noRecord:false,
            CurrentPage: 1,
            TotalPages: 0,
            ExpenseApprovalData:[],           
            Headers:[{
                        "COLUMN_NAME": "Date",
                        "DATA_TYPE": "date",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Date",
                        "DISPLAY_ORDER": 1,
                        
                    },
                    {
                        "COLUMN_NAME": "Title",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Title",
                        "DISPLAY_ORDER": 2
                    },
                    {
                        "COLUMN_NAME": "UserName",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": this.$t("UserName"),
                        "DISPLAY_ORDER": 3
                    },
                    {
                        "COLUMN_NAME": "Category",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Category",
                        "DISPLAY_ORDER": 4
                    },                    
                    {
                        "COLUMN_NAME": "Project",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": this.$t("Project_Name"),
                        "DISPLAY_ORDER": 5
                    },
                    { 
                        "COLUMN_NAME": "Amount",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Amount",
                        "DISPLAY_ORDER": 6,
                        settings:{
                            isInSlot : true
                        }
                    }                   
            ],         
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            ExpenseGetProjectList: null,
            ExpenseGetUserList:null,
            showCommentModal:false,
            CommentHeader:null,
            UserName:null,
            isSingleCommentforMultipleExpenses:null,  
            statuscode:'',
            showApproverStatusModal:false,
            IsShowAction:true,
            visibility:true,
            access:true,
            LegendArray:[],
            showData:''
           
        }
    },
    created: function(){ 
        var vueObj=this;
        Schema.bindleftCommonSearchdropdown(vueObj,"Project","no","");
        Schema.bindleftCommonSearchdropdown(vueObj,"User","","EXPENSEAPPROVAL");
        Schema.bindleftCommonSearchdropdown(vueObj,"Show Data","","");
        vueObj.FetchData();      
        vueObj.UserName=vueObj.GetUserInfo.Name;
        vueObj.LegendArray = [
            {
                class: 'legend-pending colorboxsquare',
                text: (vueObj.$t('DueExpenses', ['Expenses']))
            },
            {
                class: 'legends_profile colorboxsquare',
                text: (vueObj.$t('OverdueExpenses', ['Expenses']))
            }
        ]
    },
  
    methods:{ 
        FetchData: function(){            
            var vueObj = this;            
            vueObj.isLoading = true;
            var params = `pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&${vueObj.searchCondition}`        
             ExpenseApprovalDataServices.ExpenseApprovalListing(params).then(function(response){ 
                vueObj.isLoading = false;   
                if(response.data != null && response.data !='') 
                {
                    vueObj.ExpenseApprovalData =response.data;
                    if(vueObj.ExpenseApprovalData.length>0)
                    {  
                        for (var row in vueObj.ExpenseApprovalData) {
                            vueObj.ExpenseApprovalData[row].isCheckBoxDisabled = false;
                            vueObj.ExpenseApprovalData[row].additionalClass = '';
                            if (vueObj.ExpenseApprovalData[row].UseSequence  == null) {
                                vueObj.ExpenseApprovalData[row].isCheckBoxDisabled = false;
                            }
                            else if (vueObj.ExpenseApprovalData[row].UseSequence  == 0 && vueObj.ExpenseApprovalData[row].Status == "Approval Pending") {
                                vueObj.ExpenseApprovalData[row].isCheckBoxDisabled = false;
                            }
                            else {
                                if (vueObj.ExpenseApprovalData[row].Status  == 'Approval Pending') {
                                    vueObj.ExpenseApprovalData[row].isCheckBoxDisabled = false;
                                }else{
                                    vueObj.ExpenseApprovalData[row].isCheckBoxDisabled = true;
                                }
                            }
                            if(vueObj.ExpenseApprovalData[row].Status=="Approved"){
                                vueObj.ExpenseApprovalData[row].additionalClass = '';
                            }
                            else if(vueObj.ExpenseApprovalData[row].EXPENSESCONSIDEREDASOVERDUE  <=0){
                                vueObj.ExpenseApprovalData[row].additionalClass = 'high-bar';
                            }
                            else if(vueObj.ExpenseApprovalData[row].QuicklyExpenseMustBeApproved <=0){                                
                                vueObj.ExpenseApprovalData[row].additionalClass = 'high-profile-bar';
                            }
                            
                        }
                        vueObj.listheaderbuttons[1].isdisabled = true;
                        vueObj.listheaderbuttons[2].isdisabled = true;
                        vueObj.TotalRecords=vueObj.ExpenseApprovalData[0].TotalRecords;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;                       
                        vueObj.noRecord = false;
                    }
                    else{
                        vueObj.noRecord = true;
                        vueObj.ExpenseApprovalData = [];
                        vueObj.TotalRecords = 0
                    }
                }
                else{
                    vueObj.noRecord = true;
                    vueObj.ExpenseApprovalData = [];
                    vueObj.TotalRecords = 0
                }                 
            });           
        },      
        DisableInputBox: function (status) {            
            var returnClass = '';
            switch (status) {
                case 'Approved':
                case 'Active':
                case 'ApprovalPending':
                    returnClass = 'disbaled';
                    break;
                case 'Rejected':
                case 'SentForCorrection':
                case 'Draft':                   
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {                
                let action = { ...actionItem };
                switch (action.title){
                    case 'ViewAttchedFiles':
                        action.isVisible= row.FILESCOUNT != 0
                        break
                    case 'Comments':
                        action.isVisible=row.filescount != 0;
                        break
                    case 'Send For Correction': 
                        action.isVisible= row.commentscount != 0 || row.status_code != 'ACT';
                        break
                    case 'ApproverStatus':
                        action.isVisible= row.UseSequence == 1 || row.UseSequence == 0 && row.Status == "Approved"
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        leftCommonSearch: function (value) {            
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;            
            if (typeof value.searchCondition != 'undefined') {
              this.searchCondition = value.searchConditionJson;
              this.searchCondition =  this.searchCondition.replace("|", "&dateTo=");
            }
            else {
              this.searchCondition = "";
            }
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        actionButtonClick: function (callbackfunction, item) {               
            switch (callbackfunction) {
                case 'Edit': 
                    break;                  
                case 'openAddNewEvent': 
                    this.openAddNewEvent(item);
                    break;                 
                case 'SendForCorrection':
                    this.SendForCorrection(item);
                    break;                           
                case 'ApproverStatus':  
                    this.ApproverStatus(item);               
                    break;
                case 'downloadAttachment':
                    this.OpenFileModal(item);
                    break;
            }
        },        
        CloseExpenseAttachments:function(){
            this.showFileModal=false;
        },
        sortdata: function (s) {            
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        commonHeaderButtonClick: function (event) {                                    
            switch (event.callbackfunction) {                
                case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                    break;
                case "SendForApprovalApprove":
                    this.CheckPrivilage('EXP_APPROVED');
                    break;
                case "SendForApprovalReject":
                    this.CheckPrivilage('EXP_REJECTED');
                    break;
                case "ViewHistory":
                    this.ViewHistoryList();
                    break;                                
            }
        },
        ViewHistoryList:function(){             
            this.privilegeParams = `controller=ExpenseApproval&action=`;
            ExpenseApprovalDataServices.CheckPrivilege(this.privilegeParams + 'History').then((response) => {
                if (response.data == true) {
                    this.$router.push({ name: 'ExpenseHistory' });  
                } else {
                    this.ShowAlert(this.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
                     
        },
        CloseExpenseComments:function(){
           this.showCommentModal=false; 
        },
        CheckPrivilage:function(statusForOut){            
            var vueObj =this;
            if(statusForOut=='EXP_APPROVED'){
                vueObj.privilegeParams = `controller=ApprovalRule&action=AddApproverStatusAsync`;
            }else{
                vueObj.privilegeParams = `controller=ApprovalRule&action=AddApproverStatusAsync`;
            }
            ExpenseApprovalDataServices.CheckPrivilege(vueObj.privilegeParams).then((response) => {
                if (response.data == true) {                    
                    vueObj.commonApproval(statusForOut)
                } else {
                    vueObj.ShowAlert(vueObj.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
        },
        SendForCorrection:function(statusForOut){            
            var vueObj= this;
            vueObj.CommentHeader=`Comment for ${vueObj.UserName} Expense Approval`;
            vueObj.showCommentModal=true;
            vueObj.isSingleCommentforMultipleExpenses=1;     
            vueObj.visibility=1;
            vueObj.access=1;
            vueObj.statuscode='EXP_SEND_FOR_CORRECTION';
            vueObj.expid=statusForOut.EXPENSEID + "$" + statusForOut.ApprovalRuleId + "$" + statusForOut.ApprovalChainId + "$" + statusForOut.ApprovalGroupId + "$";;
        },
        commonApproval:function(statusForOut){  
            debugger          
            var vueObj= this;
            vueObj.CommentHeader=`Comment for ${vueObj.UserName} Expense Approval`;
            vueObj.isSingleCommentforMultipleExpenses=1;            
            var expenseId = $("input.chkItems:checked");
            var ids = '';           
            var count = 0;
            for (var i = 0; i < expenseId.length; i++) {
                var id = $(expenseId[i]).val();
                var ruleid = 0;
                var approvalChainId = 0;
                var approvalGroupId = 0;
                if (ruleid == "") {
                    ruleid = 0;
                }
                if (i == 0) {
                    ids = id + "$" + ruleid + "$" + approvalChainId + "$" + approvalGroupId + "$";
                }
                else {
                    ids = ids + "," + id + "$" + ruleid + "$" + approvalChainId + "$" + approvalGroupId + "$";
                }
                count = count + 1;
            }
            vueObj.showCommentModal=true;
            vueObj.expid=ids;
            vueObj.statuscode=statusForOut;
            vueObj.visibility=1;
            vueObj.access=1;
        },
        openAddNewEvent:function(item){
            this.privilegeParams = `controller=Comment&action=`;
            ExpenseApprovalDataServices.CheckPrivilege(this.privilegeParams + 'CommentsList').then((response) => {
                if (response.data == true) {
                    this.CommentHeader='Comment'; 
                    this.isSingleCommentforMultipleExpenses=0;          
                    this.showCommentModal=true;
                    this.statuscode = null;
                    this.expid = item.EXPENSEID;
                    this.visibility=1;
                    this.access=0;
                } else {
                    this.ShowAlert(this.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
        },
        OpenFileModal:function(item){
            this.showFileModal=true;            
            this.expid = item.EXPENSEID;
            this.statuscode = item.status_code;
        },
        ApproverStatus:function name(item) { 
            this.privilegeParams = `controller=ApprovalRule&action=`;
            ExpenseApprovalDataServices.CheckPrivilege(this.privilegeParams + 'ListPriorPendingApprovalGroup').then((response) => {
                if (response.data == true) {
                    this.showApproverStatusModal=true;     
                    this.expid = item.EXPENSEID; 
                } else {
                    this.ShowAlert(this.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });                       
                 
        },
        CloseExpenseApproverStatus:function(){            
            this.showApproverStatusModal=false;
        }
    }
}
</script>