<template>
  <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                {{$t('Attachments')}}
                            </h5>
                            <button v-on:click="CloseExpenseAttachments($event)" href="javascript:;" class="close"><i class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <div class="modal-body">
                            <div class="listing">
                            <div class="table-responsive table-fix-header" v-if="!noRecordForTab">
                                <table class="table dt-responsive nowrap mt-0" id="table-openactivities-content">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th><span>#</span></th>
                                            <th><span>FileName</span></th>
                                            <th><span>Action</span></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item,index) in attachmentsData" :key="index">
                                            <td>{{index+1}}.</td>
                                            <td>{{item.EXPENSE_FILE_NAME}}</td>
                                            <td>
                                                <a title="DownloadFile" v-on:click="downloadAttachment(item)" href="javascript:;" class="round-icon-small btn-primary-light">
                                                    <i class="fa fa-download"></i>
                                                </a>
                                                <a href="javascript:;" v-on:click="deleteAttachment(item)"  class="round-icon-small btn-danger-light" title="DeleteFile">
                                                    <i class="fa fa-trash-o"></i>
                                                </a>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div></div>
                            <div v-if="noRecordForTab" class="text-center text-danger p-4 border no-record">{{$t('NoRecordfound')}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

</template>

<script>
import ExpenseDataServices from "@/services/DataService";
export default {
    props: {
        expenseid:null,
        code: {
            type: String,
            required: true
        }
    },
    data: function (){
        return{
            isLoading:false,
            attachmentsData:[],        
            noRecordForTab: false,
            statuscode: '',
            Expense_Id:''
        }
    },
    created:function(){
        this.statuscode = this.code;
        this.Expense_Id=this.expenseid;        
        this.Fetchdata();
    },
    methods:{
        Fetchdata:function(){
            var vueObj=this;
            var url="expenseid="+vueObj.expenseid;
            ExpenseDataServices.GetExpenseFileList(url).then(function (response) {                          
                vueObj.isLoading=true;
                if(response.data.length > 0 || response.data !='')
                {
                     vueObj.attachmentsData = response.data.DATA;
                     vueObj.noRecordForTab = false;
                }
                else {
                     vueObj.noRecordForTab = true;
                     vueObj.attachmentsData = null;
                     vueObj.isLoading=false; 
                 } 
                vueObj.isLoading=false; 
            });

        },
        downloadAttachment: function (item) {                           
            var vm=this;             
            vm.isLoading=true;            
            var param=`fileId=${item.EXPENSE_ID}&filePath=${item.EXPENSE_FILE_PATH}&fileType=${item.CONTENT_TYPE}&fileName=${item.EXPENSE_FILE_NAME}&downloadType=single`;
            ExpenseDataServices.downloadAttachment(param).then(function(response){                
                vm.isLoading=false;
                var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', (item.EXPENSE_FILE_NAME));
                document.body.appendChild(fileLink);
                fileLink.click();
           });            
        },
        deleteAttachment: function (item) {     
                var vm = this;
                vm.confirmR(this.$t("ConfirmDelete"), true, (this.$t("Delete") + "  " + this.$t("Attachment")), function () {
                vm.isLoading=true;
                var param=`fileId=${item.EXPENSE_FILE_ID}&expId=${item.EXPENSE_ID}`;
                ExpenseDataServices.DeleteAttachment(param).then(function (response) {                                        
                    vm.isLoading=false;
                    if (response.status == 200) {
                        vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Attachment')]), "success", true, vm.$t("Alert"));  
                        vm.Fetchdata();
                    }
                });
            });            
        }, 
        CloseExpenseAttachments: function(item){
            this.$emit('CloseExpenseAttachments',item)
        }
    }
}

</script>