
<template>
   
    
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                {{$t('ApproverStatus')}}
                            </h5>
                            <button v-on:click="CloseExpenseApproverStatus(event)" href="javascript:;" class="close"><i class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <div class="modal-body">
                            <div class="table-responsive table-fix-header" v-if="!noRecordForTab">
                                <table class="table dt-responsive nowrap mt-0" id="table-openactivities-content">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th><span>{{$t('ApprovalGroupName')}}</span></th>
                                            <th><span>{{$t('Header_User')}}</span></th>
                                            <th><span>{{$t('Status')}}</span></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item,index) in ExpenseApproverStatusData" :key="index">
                                            <td>{{item.APPROVAL_GROUP_NAME}}</td>
                                            <td>{{item.USERS}}</td>
                                            <td>{{item.STATUS}}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            <div v-if="noRecordForTab" class="text-center text-danger p-4 border no-record">{{$t('NoRecordfound')}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
  
</template>

<script>
import ExpenseApprovalDataServices from "@/services/DataService";
export default {
     props: {
        expenseid: {
            type: String,
            required: true
        }
     },
    data:function (){
        return{
            isLoading:false,
            ExpenseApproverStatusData:null,           
            noRecordForTab:false
        }
    },
     created: function(){ 
        this.FetchData();
    },
    methods:{
        CloseExpenseApproverStatus: function(item){
            this.$emit('CloseExpenseApproverStatus',item)
        },
        FetchData:function (){          
            var vueObj=this;  
            vueObj.isLoading=true;          
            var param=`id=${vueObj.expenseid}&approvalTypeId=2&approvalChainId=0&approverOrder=0`;
            ExpenseApprovalDataServices.ExpenseApproverStatusListing(param).then(function(response){                                              
                if(response.data != null){
                    if(response.data.length>0)
                    {
                       vueObj.ExpenseApproverStatusData=response.data; 
                    }
                    else{
                        vueObj.noRecordForTab = true;
                        vueObj.ExpenseApproverStatusData = [];                       
                    }
                }
                else{
                    vueObj.noRecordForTab = true;
                    vueObj.ExpenseApproverStatusData = [];                  
                    
                }
                vueObj.isLoading=false;
            });
        }
    }
}
</script>